var protos = require('./protos');

var TRANSACTION_FIELD_MAP = new Map([[protos.CreateAccountTransaction, 'createAccount'], [protos.DeployContractTransaction, 'deployContract'], [protos.FunctionCallTransaction, 'functionCall'], [protos.SendMoneyTransaction, 'sendMoney'], [protos.StakeTransaction, 'stake'], [protos.SwapKeyTransaction, 'swapKey'], [protos.AddKeyTransaction, 'addKey'], [protos.DeleteKeyTransaction, 'deleteKey']]);

module.exports.getTransactionFieldName = function (transactionProto) {
  return TRANSACTION_FIELD_MAP.get(transactionProto.constructor);
};